<template>
    <v-app>
      <v-main>
          <v-form v-model="valid" width="500" class="form-registro mx-auto mt-9">
            <v-card-title class="titulo">Ingreso de Usuario</v-card-title>
            <v-card-text>
              <v-text-field v-model="email" id="caja"   class="form-input" label="Usuario" color="#1DE9B6"  required  :rules="nameRules" prepend-icon="mdi-account-circle"/>
              <v-text-field v-model="pass" id="caja" class="form-input"  label="Contrase√±a" color="#1DE9B6" required  :rules="passRules" :type=" mostrarContrase√±a ? 'text' : 'password'"
                      prepend-icon="mdi-lock"
                      :append-icon="mostrarContrase√±a ? 'mdi-eye' : 'mdi-eye-off'"
                      @click:append="mostrarContrase√±a =! mostrarContrase√±a"
                      v-on:keyup.enter="login()"
              />
            </v-card-text>  
            <v-divider></v-divider>
            <v-card-actions >
              <input  class="boton" type="button"  @click="login()" value="Ingresar">                   
            </v-card-actions>
          </v-form >
      </v-main>
      <!--barra del piso-->
      <template>
        <v-footer dark padless >
          <v-card  class="flex" flat  tile >
            <v-card-title >
              <strong class="subheading"></strong>
              <v-spacer></v-spacer>
            </v-card-title>
            <v-card-text  class="py-2 white--text text-center"> {{ new Date().getFullYear() }} ‚Äî <strong>Proyecto</strong> </v-card-text>
          </v-card>
        </v-footer>
      </template>
    </v-app>
    
</template>
<script>

import axios from "axios"
import Swal from 'sweetalert2'

export default {
    data() {
      return {
        mostrarContrase√±a:false,
        email:"",
        pass:"",
        msgError:'',
        nameRules: [
            v => !!v || 'Email requerido',
            v => v.length <= 20 || 'Email supero los 20 caracteres',
        ],
        passRules: [
            v => !!v || 'Contrase√±a requerida',
            v => v.length <= 20 || 'Contrase√±a supera los 20 caracteres',
        ],
        valid: false,
      }//return
    },//data
    methods:{
      msjcompra:function(tata){
        Swal.fire({
          position: 'top',
          icon: 'error',
          title: tata,
          showConfirmButton: false,
          //5000 son 5 seg
          timer: 2000})
      },
      login(){
        if (!this.email || !this.pass) {
          this.msgError = 'Email / contrase√±a  oblicagorios';
          this.msjcompra(this.msgError);
        }else{        
          axios.post("usuario/login",{email:this.email, password:this.pass})
            .then(response =>{
              this.$store.dispatch("setToken", response.data.token);
              this.$store.dispatch("setRol", response.data.usuario.rol);
              this.$store.dispatch("setIdUser", response.data.usuario._id);
              this.$store.dispatch("setName", response.data.usuario.nombre);
              this.$router.push("/home");
              console.log('token' + response.data.token);
              return console.log(response);
            })
            .catch((error)=>{
              if(!error.response.data.msg){
                console.log(error.response);
                this.msgError = error.response.data.errors[0].msg
                this.msjcompra(this.msgError);
              }else{
                this.msgError = error.response.data.msg
                console.log(error.response.data.msg);
                this.msjcompra(this.msgError);
              }
            })
        }
      },
    }//methods
}
</script>

